.loadingFacebook {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}

.loadingFacebook div {
  display: inline-block;
  position: absolute;
  left: 6px;
  width: 13px;
  background: #d2d7dc;
  animation: loadingFacebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}

.loadingFacebook div:nth-child(1) {
  left: 6px;
  animation-delay: -0.24s;
}

.loadingFacebook div:nth-child(2) {
  left: 26px;
  animation-delay: -0.12s;
}

.loadingFacebook div:nth-child(3) {
  left: 45px;
  animation-delay: 0;
}

@keyframes loadingFacebook {
  0% {
    top: 6px;
    height: 51px;
  }
  50%,
  100% {
    top: 19px;
    height: 26px;
  }
}
